import React from 'react';
import './Product.css';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';

function Product({image, title, seller,ticker, price, price_team}) {
    return (
     
    <div className="product">

       <div className="categories">
            <h2>Trending</h2>
            <h2>Recommended</h2>
            <h2>Clothes</h2>
            <h2>Tech</h2>
            <h2>Food</h2>
            <h2>Makeup</h2>
    </div>
        <img className="pic" width="100%" src={image} alt="product"/>
        <div className="Rectangle1"><p className="classifier">Trended</p></div>  
        <h2 className="title">{title}</h2>
        <p className="seller">By {seller}</p>
       {/* <div className="Rectangle4">
            <InfoOutlinedIcon className="icon"/>
            <p className="Delivery">Delivery in 7 days</p>
        </div>*/}
        <div className="bar">
        <div className="solo">
            <div className="price">${price}</div>
            <div className="purchase">Solo Purchase</div>
        </div>
        <div className="team">
            <div className="price">${price_team}</div>
            <div className="purchase">Team Purchase</div>
        </div>
</div>
    </div>
    
    )
}

export default Product;
